/* Layout Geral */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.App {
  display: flex;
}

/* Área de fundo */
.content {
  margin-left: 200px; /* Mesma largura da navbar */
  padding: 20px;
  width: calc(100% - 200px);
  background-color: #000000;
  min-height: 100vh;
  box-sizing: border-box;
  color: white;
}

body {
  color: #333;
  background-color: #000000;
  line-height: 1.6;
}

/* Seções */
.App {
  text-align: center;
}

section {
  padding: 4rem 1rem;
}

/* Navbar fixa na lateral esquerda */
.side-navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 200px;
  height: 100%;
  background-color: #002570; /* Cor de fundo */
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 10px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

/* Logo no topo da navbar */
.side-navbar .logo {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 20px;
}

/* Container de botões */
.navbar button {
  display: block; /* Para alinhar verticalmente */
  width: 100%; /* Ajusta o tamanho do botão */
  margin: 20px auto; /* Centraliza o botão na navbar */
  padding: 10px 30px;
  font-size: 16px;
  font-weight: bold;
  color: #002570; /* Cor do texto */
  background: linear-gradient(145deg, #e0e0e0, #c0c0c0); /* Efeito falso 3D */
  border: none;
  border-radius: 25px; /* Cantos arredondados */
  box-shadow: 4px 4px 8px #aaaaaa, -4px -4px 8px #ffffff; /* Sombra para falso 3D */
  cursor: pointer;
  transition: all 0.3s ease;
}

.navbar button:hover {
  background: linear-gradient(145deg, #c0c0c0, #e0e0e0); /* Inverte o gradiente no hover */
  box-shadow: 2px 2px 5px #aaaaaa, -2px -2px 5px #ffffff; /* Sombra reduzida no hover */
  transform: scale(1.05); /* Leve zoom no hover */
}

.navbar button:active {
  background: #b0b0b0; /* Cor mais escura no clique */
  box-shadow: inset 2px 2px 5px #aaaaaa, inset -2px -2px 5px #ffffff; /* Sombra invertida no clique */
}


/* About */
.about-section {
  background-color: #000000;
  color: #ffffff;
}

.about-section h2 {
  font-size: 2rem;
  color: #0044cc;
  margin-bottom: 1rem;
}

.about-section p {
  max-width: 600px;
  margin: 0 auto;
  font-size: 1.1rem;
}

/* Projects */
.projects-section {
  background-color: #000000;
}

.projects-section h2 {
  font-size: 2rem;
  color: #0044cc;
  margin-bottom: 2rem;
}

.project-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
}

.project-card {
  background-color: #f8f9fa;
  border: 1px solid #ddd;
  padding: 1.5rem;
  width: 100%;
  max-width: 300px;
  text-align: center;
  border-radius: 8px;
  transition: transform 0.2s ease;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.project-card p {
  font-size: 1rem;
  color: #555;
}

.project-link,
.gh-link {
  display: inline-block;
  margin-top: 0.5rem;
  color: #0044cc;
  text-decoration: none;
  font-weight: bold;
}

.project-link:hover,
.gh-link:hover {
  text-decoration: underline;
}

/* Contact */
.contact-section {
  background-color: #2e2e2e;
}

.contact-section h2 {
  font-size: 2rem;
  color: #0044cc;
  margin-bottom: 1rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: 0 auto;
}

.contact-form input,
.contact-form textarea {
  padding: 0.5rem;
  margin: 0.5rem 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
}

.contact-form input[type="submit"] {
  background-color: #0044cc;
  color: #fff;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.contact-form input[type="submit"]:hover {
  background-color: #002e8a;
}

/* Links e Títulos Globais */
h2 {
  text-align: center;
  margin-bottom: 1.5rem;
}

a {
  color: #0044cc;
  text-decoration: none;
}

a:hover {
  color: #002e8a;
}

/* Geral */
#projects {
  padding: 20px;
  background-color: #000000;
}

#projects h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #ffffff;
}

/* Layout de cards */
.project-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  justify-content: center;
  align-items: stretch;
}

/* Estilo dos cards */
.project-card {
  background: #002570;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}

.project-card h3 {
  color: #1a73e8;
  font-size: 1.2em;
  margin-bottom: 10px;
}

.project-card p {
  color: #ffffff;
  margin-bottom: 20px;
  font-size: 0.95em;
}

.card-links {
  margin-top: auto;
}

.card-links .btn {
  display: inline-block;
  padding: 10px 15px;
  margin: 5px;
  font-size: 0.9em;
  color: white;
  background-color: #1a73e8;
  border-radius: 5px;
  text-decoration: none;
  transition: background 0.3s ease;
}

.card-links .btn:hover {
  background-color: #1558b0;
}

.card-links .btn.secondary {
  background-color: #555;
}

.card-links .btn.secondary:hover {
  background-color: #333;
}
